import React, { Component } from 'react';
import { connect } from 'react-redux';
import html2canvas from 'html2canvas';
import JsPDF from 'jspdf';
import FabButton from '../../presentational/FabButton/FabButton';

interface Props {
  userName: string;
  className?: string;
}

class ToPdfContainer extends Component<Props> {

  imageToPdf = (imageBase64: string) => {
    const imageWidth = 210; // A4 width in mm
    const imageHeight = 297; // A4 height in mm

    const pdfDoc = new JsPDF();
    pdfDoc.addImage(imageBase64, 'png', 0, 0, imageWidth, imageHeight);
    pdfDoc.save(`${this.props.userName} - CV.pdf`);
  };

  handleSavePdf = () => {
    const htmlElement: HTMLElement | null = document.querySelector('#my-cv');
    const imageQuality = 1; // 0 to 1

    html2canvas(htmlElement as HTMLElement).then((canvas: HTMLCanvasElement)=> {
      const image: string = canvas.toDataURL('image/jpeg', imageQuality);
      this.imageToPdf(image);
    });
  };

  render() {
    return (
      <FabButton
        iconClassName='far fa-file-pdf'
        onClick={this.handleSavePdf}
        style={{ backgroundColor: '#222' }}
      />
    );
  }
}

const mapStateToProps = (state: any) => {
  return {
    userName: state.paper.heading.name,
  }
};

export default connect(mapStateToProps)(ToPdfContainer);