import React, { Component, ChangeEvent } from 'react';
import { css, StyleSheet } from 'aphrodite';
import withTheme from '../../hoc/withTheme';

interface IProps {
  avatarImage: string;
  avatarRotation: number;
  theme: any;
  updateAvatar: (name: string | ArrayBuffer | null) => void;
  updateRotation: (actionName: 'reset' | any) => void;
}

const styles = StyleSheet.create({
  avatar: {
    display: 'block',
    position: 'relative',
    border: 'none',
    background: 'none',
    margin: '0 1.07cm 0 15px',
    padding: 0,
    transition: 'transform 0.3s ease-out',
    flexShrink: 0,
    width: 210,
    ':hover': {
      transform: 'scale(1.05)',
    },
    ':hover button': {
      opacity: 1,
    }
  },
  image: {
    alignItems: 'center',
    backgroundSize: 'cover',
    backgroundPosition: '50% 50%',
    borderRadius: '50%',
    boxSizing: 'border-box',
    display: 'flex',
    fontWeight: 400,
    textTransform: 'uppercase',
    justifyContent: 'center',
    height: '210px',
    width: '100%',
    transition: 'transform 0.3s',
  },
  hidden: {
    position: 'absolute',
    left: 0,
    top: 0,
    opacity: 0,
    width: '100%',
    height: '100%',
    cursor: 'pointer',
    outline: 'none',
  },
  info: {

  },
  rotateBtn: {
    background: 'none',
    border: 'none',
    cursor: 'pointer',
    opacity: 0,
    outline: 'none',
    position: 'absolute',
    top: 0,
    right: 0,
    zIndex: 1,
    transition: 'opacity 300ms, transform 300ms',
    ':hover': {
      transform: 'rotate(180deg)',
    }
  },
});

class Avatar extends Component<IProps> {
  private handleChange = (evt: ChangeEvent<HTMLInputElement>) => {
    if (evt.target.files) {
      let reader = new FileReader();
      let file = evt.target.files[0];

      reader.onloadend = () => {
        this.props.updateAvatar(reader.result);
        this.props.updateRotation('reset');
      };

      if (file) {
        reader.readAsDataURL(file);
      }
    }
  };

  public render() {
    const { avatarImage, updateRotation, avatarRotation, theme } = this.props;
    const isImageUploaded = avatarImage.length !== 0;

    return (
      <div className={css(styles.avatar)}>
        { isImageUploaded && <button className={css(styles.rotateBtn)} onClick={updateRotation}><i className={'fas fa-sync'} /></button> }
        <button
          className={css(styles.image)}
          style={
            {
              backgroundImage: `url(${avatarImage})`,
              transform: `rotate(${isImageUploaded ? avatarRotation : 0}deg)`,
              border: isImageUploaded ? 'none' : `1px solid ${theme.color}`,
              color: theme.color,
            }
          }
        >
          { isImageUploaded || <p className={css(styles.info)}>Upload your photo here</p> }
        </button>
        <input
          type="file" id="upload"
          accept="image/png, image/jpeg"
          className={css(styles.hidden)}
          onChange={this.handleChange}
        />
      </div>
    )
  }
}

export default withTheme(Avatar);