import React, { ChangeEvent, Component } from 'react';
import { css, StyleSheet } from 'aphrodite';
import { ReactSelectOption } from '../../containers/ToolbarContainer/constants';
import LocalStorageConnect from '../../containers/LocalStorageConnect';
import ColorPicker from '../ColorPicker';
import FontDropdown from './FontDropdown';
import FontSizeDropdown from './FontSizeDropdown';

interface OwnProps {
  settings: any,
  updateTheme: (category: string, value: number | string) => void;
  toggleContent: (category: string) => void;
  fontOptions: ReactSelectOption[],
}

const styles = StyleSheet.create({
  header: {
    display: 'flex',
    alignItems: 'center',
    backgroundColor: '#333',
    borderRadius: 15,
    position: 'fixed',
    zIndex: 10,
    margin: '0 auto',
  },
  toolbarItem: {
    display: 'flex',
    justifyContent: 'center',
    position: 'relative',
    zIndex: 1,
  },
  btn: {
    background: 'none',
    border: 'none',
    color: '#fff',
    cursor: 'pointer',
    fontSize: '1.5em',
    margin: '0.20em',
    transition: 'transform 0.3s',
    ':hover': {
      transform: 'scale(1.1)',
    },
    ':focus': {
      transform: 'scale(1.1)',
    }
  },
  btnOn: {
    opacity: 1,
  },
  btnOff: {
    opacity: 0.5,
  },
  divider: {
    width: 1,
    height: 30,
    margin: '0 1em',
    backgroundColor: '#fff',
  },
  clickCapture: {
    position: 'fixed',
    top: 0,
    left: 0,
    width: '100vw',
    height: '100vh',
    zIndex: 9,
  }
});

const Divider:React.FunctionComponent = () => <div className={css(styles.divider)} />;

const ToolbarItem: React.FunctionComponent<any> = ({ InsideComponent, isVisibleComponent, icon, onClick }) => {
  return (
    <div className={css(styles.toolbarItem)}>
      <button className={css(styles.btn)} onClick={onClick}>
        <i className={icon} />
      </button>
      { isVisibleComponent && <InsideComponent /> }
    </div>
  )
};

interface State {
  fontFamilyOpen: boolean,
  fontSizeOpen: boolean,
  paletteOpen: boolean,
  [index: string]: boolean | number,
}

let lastScrollY = 0;
let ticking = false;

class Toolbar extends Component<OwnProps, State> {
  state = {
    fontFamilyOpen: false,
    fontSizeOpen: false,
    paletteOpen: false,
    topAdjustment: 0,
  };

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll = () => {
    lastScrollY = window.scrollY;

    if (!ticking) {
      window.requestAnimationFrame(() => {
        this.setState({
          topAdjustment: Math.min(window.document.documentElement.scrollTop, 60),
        });
        ticking = false;
      });
      ticking = true;
    }
  };

  handleChangeColor = (evt: ChangeEvent<HTMLInputElement>) => {
    this.props.updateTheme('color', evt.target.value);
  };

  handleChangeFont = (evt: any) => {
    this.props.updateTheme('fontFamily', evt.value);
  };

  handleToggleContent = (category: string) => () => {
    this.props.toggleContent(category);
  };

  handleCloseAllMenus = () => {
    for (let stateKey in this.state) {
      if (stateKey !== 'topAdjustment') {
        this.setState({[stateKey]: false})
      }
    }
  };

  handleToggleDownMenu = (category: string) => () => {
    this.handleCloseAllMenus();
    // @ts-ignore
    if (this.state[category]) {
      this.setState({ [category]: false });
    } else {
      this.setState({ [category]: true });
    }
  };

  isMenuDisplayed = () => {

    return Object.values(this.state)
      .filter(stateValue => typeof stateValue !== 'number')
      .some(stateValue => !!stateValue);
  };

  applyOpacity = (isOn: boolean) => {
    return isOn
      ? styles.btnOn
      : styles.btnOff;
  };

  render() {
    const { contentOn } = this.props.settings;

    return (
      <React.Fragment>
        <header className={css(styles.header)} style={{ top: `calc(11.5vh - ${this.state.topAdjustment}px)`}}>
          {/*<Select options={this.props.fontOptions} onChange={this.handleChangeFont} />*/}
          <ToolbarItem
            onClick={this.handleToggleDownMenu('fontFamilyOpen')}
            isVisibleComponent={this.state.fontFamilyOpen}
            icon="fas fa-font"
            InsideComponent={FontDropdown}
          />
          <ToolbarItem
            onClick={this.handleToggleDownMenu('fontSizeOpen')}
            isVisibleComponent={this.state.fontSizeOpen}
            icon="fas fa-text-height"
            InsideComponent={FontSizeDropdown}
          />
          <ToolbarItem
            onClick={this.handleToggleDownMenu('paletteOpen')}
            isVisibleComponent={this.state.paletteOpen}
            icon="fas fa-palette"
            InsideComponent={ColorPicker}
          />
          <Divider />
          <button className={css(styles.btn, this.applyOpacity(contentOn.avatar))} onClick={this.handleToggleContent('avatar') }>
            <i className="fas fa-user" />
          </button>
          <button className={css(styles.btn, this.applyOpacity(contentOn.profile))} onClick={this.handleToggleContent('profile')}>
            <i className="fas fa-address-card" />
          </button>
          <button className={css(styles.btn, this.applyOpacity(contentOn.personal))} onClick={this.handleToggleContent('personal')}>
            <i className="fas fa-info" />
          </button>
          <button className={css(styles.btn, this.applyOpacity(contentOn.skills))} onClick={this.handleToggleContent('skills')}>
            <i className="fas fa-pencil-ruler" />
          </button>
          <button className={css(styles.btn, this.applyOpacity(contentOn.certifications))} onClick={this.handleToggleContent('certifications')}>
            <i className="fas fa-trophy" />
          </button>
          <Divider />
          <LocalStorageConnect />
        </header>
        { this.isMenuDisplayed() && <div className={css(styles.clickCapture)} onClick={this.handleCloseAllMenus}/> }
      </React.Fragment>
    );
  }
}

export default Toolbar;