import React, { Component } from 'react';
import { css, StyleSheet } from 'aphrodite';

import HeadingContainer from '../../containers/PaperContainer/HeadingContainer';
import ProfileContainer from '../../containers/PaperContainer/ProfileContainer';
import PersonalDetailsContainer from '../../containers/PaperContainer/PersonalDetailsContainer';
import WorkExperience from './WorkExperience';
import Education from './Education';
import Skills from './Skills';
import AdditionalInfo from './AdditionalInfo';
import ContactContainer from '../../containers/PaperContainer/ContactContainer';
import withTheme from '../../hoc/withTheme';
import { animatePaper, animateSections } from './initialAnimation';

const styles = StyleSheet.create({
  paper: {
    backgroundColor: '#fff',
    margin: '0 auto',
    width: '21cm',
    height: '29.7cm',
    padding: '2.54cm 2cm 2.54cm 2.54cm',
    position: 'relative',
    boxShadow: 'rgba(0,0,0,0.3) 5px 5px 25px 1px',
},
  head: {
    marginBottom: '1em',
  },
  body: {
    display: 'flex',
  },
  details: {
    display: 'flex',
    flexDirection: 'column',
    width: '40%',
    maxWidth: '30%',
    paddingRight: 0
  },
  mainInfo: {
    width: '70%',
    margin: '0',
    paddingLeft: 'calc(1.27cm - 0.5cm)'
  },
  footer: {

  },
  bottomMargin: {
    position: 'absolute',
    backgroundColor: '#eee',
    bottom: 0,
    left: 0,
    width: '100%',
    height: '2.54cm',
  }
});

interface IProps {
  settings: any,
}

class Paper extends Component<IProps> {
  render() {
    const {
      certifications,
      personal,
      profile,
      skills,
    } = this.props.settings.contentOn;

    return (
      <div className={css(styles.paper) + ' ' + animatePaper()} id="my-cv" style={{ fontSize: `${this.props.settings.theme.fontSize}em` }}>
        <section className={css(styles.head) + ' ' + animateSections(3)}>
          <HeadingContainer />
        </section>
        <section className={css(styles.body) + ' ' + animateSections(5)}>
          <aside className={css(styles.details)}>
            { profile && <ProfileContainer /> }
            { personal && <PersonalDetailsContainer /> }
            <ContactContainer />
          </aside>

          <summary className={css(styles.mainInfo)}>
            <WorkExperience />
            <Education />
            { skills && <Skills /> }
            { certifications && <AdditionalInfo /> }
          </summary>
        </section>
        <section className={css(styles.footer)}>

        </section>
        <div className={css(styles.bottomMargin)} />
      </div>
    );
  }
}

export default withTheme(Paper);